import React from 'react'



class Form extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            currentTimeLeft: 0,
            seconds: 0,
            minutes: 0,
        }
        this.increaseHandler = this.increaseHandler.bind(this)
        this.decreaseHandler = this.decreaseHandler.bind(this)
    }

    increaseHandler(event){
        this.setState({
            currentTimeLeft : this.state.currentTimeLeft + 1800,
            seconds: this.state.seconds,
            minutes: this.state.minutes
        })

        event.preventDefault()
    }

    decreaseHandler(event){
        if(this.state.currentTimeLeft !== 0){
        this.setState({
            currentTimeLeft: this.state.currentTimeLeft - 1800,
        })
        alert('哈哈傻逼')
        }else if(this.state.currentTimeLeft === 0){
            alert('他妈的都0了还按')
        }

        event.preventDefault()
    }

    everyUpdate(){
    if(this.state.currentTimeLeft > 1){
        this.setState({
            currentTimeLeft: this.state.currentTimeLeft - 1,
            })
        } else if (this.state.currentTimeLeft < 1){
            this.setState({
                currentTimeLeft: 0,
                seconds : 0, 
                minutes : 0
            })

        }
    }

    componentDidMount(){
        this.newUpdate = setInterval(
            () => this.everyUpdate(), 1000
        )
    }

    componentWillUnmount(){
        clearInterval(this.newUpdate)
    }
    render(){
        if(this.state.currentTimeLeft > 1 ){
            this.state.seconds = this.state.currentTimeLeft % 60 > 10 
                                 ? '0'+ this.state.currentTimeLeft % 60 : this.state.currentTimeLeft % 60
            this.state.minutes = Math.floor(this.state.currentTimeLeft / 60)
        }
        
        return(
        <div>
            <div id="TimerContainer">
                <div className="MainContainer">
                   <h1 id="number">{this.state.minutes} : {this.state.seconds}</h1>
                </div>
                <form>
                    <button name="buttonIncrease"  className="setButtons" onClick={this.increaseHandler}>增加</button>< br/>
                    <button name="buttonDecrease" className="setButtons" onClick={this.decreaseHandler}>傻逼</button>
                </form>
            </div>
            <hr />
        </div>
        )
    }
}

export default Form